import {request} from './request'



//登录
export function login(loginInfo) {
  return request({
	url:'/vue/VueProjectData/manager/login.php',
    params:{
		loginInfo
    }
  });
}

export function register(registerInfo) {
  return request({
	url:'/vue/VueProjectData/manager/register.php',
    params:{
		registerInfo
    }
  });
}

