<template>
	<div id="login">
		<!--顶部菜单-->
		<nav-bar class="login-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
			</div>
			<div slot="center">
				登录
			</div>
		</nav-bar>
		<login-content @visitorClick="visitorClick" @loginClick="loginClick" :login-info="loginInfo"/>
	</div>
</template>

<script>


import NavBar from 'components/common/navbar/NavBar'
import LoginContent from './childComps/LoginContent'

import {login} from 'network/login'

export default {
  name: 'Login',
  components:{
	NavBar,
	LoginContent,
  },
   data(){
	return{
		loginInfo:{
			username:'',
			password:'',
		},
		message:'',
	}
  },
  //https://blog.csdn.net/weixin_42406046/article/details/80624932
  methods:{
	loginClick(){
		//验证数据是否为空
		if(!this.loginInfo.username ||!this.loginInfo.password){
			this.$toast.show("账号或者密码不能为空",2000)
			return false
		}
		if(this.loginInfo.username.length<2 || this.loginInfo.username.length>18 ){
			this.$toast.show("用户名长度：2~18字符",2000)
			return false
		}
		if(this.loginInfo.password.length<6 || this.loginInfo.password.length>18 ){
			this.$toast.show("密码长度：6~18字符",2000)
			return false
		}
		
		//发送请求
		login(this.loginInfo).then(res=>{
			//判断是否登录成功
			if(res.status===0){
				//登录成功-设置缓存值
				this.$store.dispatch('setToken',res.token)
				this.$store.dispatch('setUser',res.username)
				this.loginInfo.username=''
				this.loginInfo.password=''
				this.$router.push("/mine").catch(res=>{res})		//跳到我的页面
				this.$toast.show('欢迎登录,'+res.username,2000)
				
				//console.log(localStorage.getItem('token'))
				//console.log(localStorage.getItem('username'))
			}else{
				//登录失败
				this.$toast.show(res.message,2000)
			}
		})
		//console.log(this.loginInfo)
	},
	//体验登录
	visitorClick(){
		this.loginInfo.username='测试'
		this.loginInfo.password='123456'
		this.loginClick()
	},
	//返回按钮
	backClick(){
		this.$router.go(-1)
	},
  },
  created(){
	document.title="登录"
  }
}
</script>

<style scoped>
.login-nav-bar{
	background:#efefef;
	font-size:17px;
	color:black;
	font-weight:600;
}
#login{
	height:100vh;
	position:relative;
	z-index:11;
	background:#fff;
}
  .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:10px;
  }

</style>
